.main-navigation {
  width: 320px;
  margin: 0 auto;
}
.main-navigation__menu-block {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 320px;
  box-sizing: border-box;
  padding: 25px;
  padding-bottom: 15px;
}
.main-navigation--scroll .main-navigation__menu-block {
  position: fixed;
  top: 0;
  left: calc(50% - 160px);
  background-color: #ffffff;
  z-index: 50;
}
.main-navigation__logo-link {
  display: block;
  width: 100px;
}
.main-navigation__logo {
  width: 100%;
}
.main-navigation--scroll .main-navigation__logo-link .main-navigation__logo--white {
  display: none;
}
.main-navigation--scroll .main-navigation__logo-link .main-navigation__logo--blue {
  display: block;
}
.main-navigation__toggle {
  border: none;
  font-family: inherit;
  background-color: transparent;
  cursor: pointer;
  display: block;
  width: 25px;
  height: 25px;
  padding: 0;
  margin: 0;
}
.main-navigation__toggle::before,
.main-navigation__toggle::after {
  content: "";
  display: block;
  width: 25px;
  height: 3px;
}
.main-navigation--closed .main-navigation__toggle::before,
.main-navigation--closed .main-navigation__toggle::after {
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--scroll .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after,
.main-navigation--scroll .main-navigation__toggle::after {
  background-color: #cccccc;
}
.main-navigation--closed .main-navigation__toggle::before {
  margin-bottom: 5px;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  margin-top: -3px;
  transform: rotate(-45deg);
}
.main-navigation--no-js .main-navigation__toggle {
  display: none;
}
.main-navigation__menu {
  display: block;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 5;
  width: 100%;
  box-sizing: border-box;
  padding: 25px;
  padding-top: 65px;
  background-color: #ffffff;
  z-index: 10;
}
.main-navigation__menu-list {
  margin: 0;
