  text-align: center;
  font-size: 30px;
  font-weight: 500;
  line-height: 36px;
  text-transform: uppercase;
  color: #161c35;
}
.main-navigation__log-in-btn {
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 40px;
}
.main-navigation__contacts {
  width: 270px;
