.range__level-wrapper {
  position: relative;
  display: flex;
  margin: 0 auto;
  margin-bottom: 20px;
}
.range__level-wrapper::before {
  position: absolute;
  top: 50%;
  margin-top: -5px;
  left: 50%;
  margin-left: -8px;
  content: "";
  width: 16px;
  height: 10px;
  background-color: #ffffff;
}
.range__level-wrapper::after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -8px;
  content: "";
  width: 16px;
  height: 1px;
  background-color: #cbced9;
}
.range__level {
  width: 90px;
  padding: 10px;
  text-align: center;
  border-radius: 4px;
  border: 1px solid #cbced9;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
  color: #1d2e5b;
}
.range__level--min {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: none;
}
.range__level--max {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.range__level:hover {
  border-color: #959bb2;
}
.range__level:active {
  border-color: #161c35;
}
.range__controls {
  position: relative;
}
.range__scale {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  height: 2px;
  border-radius: 0.5px;
  background-color: rgba(29, 46, 91, 0.2);
}
.range__bar {
  width: 70%;
  height: 2px;
  border-radius: 0.5px;
  margin-left: 27%;
  background-color: #1d2e5b;
}
.range__toggle {
  position: absolute;
  bottom: -4px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #1d2e5b;
  cursor: pointer;
}
.range__toggle--min {
  left: 25%;
}
.range__toggle--max {
  left: 95%;
}
.range__toggle:hover {
  transform: scale(1.4);
}
.range__toggle:active {
  box-shadow: 0px 3px 7px 0 rgba(29, 46, 91, 0.35);
}
